const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #0e0e0e;
  color: #ffffff;
  padding-bottom: 4rem;
`;

const Title = styled.div`
  margin: 4rem 0;
  font-size: 3rem;
`;

